<template>
    <div class="home">
      <el-form :model="ruleForm" disabled ref="ruleForm" label-width="100px" class="ruledemo-Form" size="mini">
        <el-form-item label="物品名称" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="物品类型" prop="type">
          <el-select v-model="ruleForm.type" placeholder="请选择物品类型">
            <el-option label="书籍" value="book"></el-option>
            <el-option label="数码产品" value="digital"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="物品价格" prop="price">
          <el-input v-model="ruleForm.price"></el-input>
        </el-form-item>
        <el-form-item label="物品图片" prop="images">
          <el-upload
            :action=url
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :file-list="imglist">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="物品描述" prop="description">
          <div style="border: 1px solid #ccc;">
            <Toolbar
              style="border-bottom: 1px solid #ccc"
              :editor="editor"
              :defaultConfig="toolbarConfig"
              :mode="mode"
          />
          <Editor
              style="height: 400px; overflow-y: hidden;"
              v-model="html"
              :defaultConfig="editorConfig"
              :mode="mode"
              @onCreated="onCreated"
          />
        </div>
        </el-form-item>
      </el-form>
      <el-button style="margin-left: 120px;" type="primary" @click="By()">立即网购</el-button>
    </div>
  </template>
  
  <script>
//   import upload from '../api/base'
    import upload from '../../api/base'
    import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
    export default {
      components: { Editor, Toolbar },
      computed:{
        
      },
      data(){
        return{
          dialogImageUrl: '',
          imglist: [],
          dialogVisible: false,
          url:upload.upload,
          ruleForm: {
            name: '',
            type: '',
            price: '',
            images:[],
            description:'',
          },
          editor: null,
          html: '<p>hello</p>',
          toolbarConfig: { },
          editorConfig: { placeholder: '请输入内容...' },
          mode: 'default', // or 'simple'
        }
      },
      methods:{
        onCreated(editor) {
          this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
          this.editor.disable()
        },
        handlePictureCardPreview(file) {
          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
        },
        By(){
            let orderId=this.$route.query.orderid
            let price=this.ruleForm.price
            this.$api.getzfb({
                    orderId:orderId,
                    price:price
                }).then(res=>{
                    console.log(res.data);
                    window.location.href=res.data.result
                })
        }
      },
      mounted() {
          // 模拟 ajax 请求，异步渲染编辑器
          // setTimeout(() => {
          //     this.html = '<p>模拟 Ajax 异步设置内容 HTML</p>'
          // }, 1500)
        let orderid=this.$route.query.orderid
        //   console.log(orderid);
         this.$api.getdetails({orderid:orderid}).then(res=>{
            console.log(res.data.data);
            this.ruleForm=res.data.data[0]
            this.ruleForm.images=JSON.parse(res.data.data[0].images)
            for(let i=0;i<this.ruleForm.images.length;i++){
              // this.imglist[i].url=this.ruleForm.image[i]
              this.imglist.push({url:this.ruleForm.images[i]})
            }
            this.html=this.ruleForm.description
         })   
      },
      beforeDestroy() {
          const editor = this.editor
          if (editor == null) return
          editor.destroy() // 组件销毁时，及时销毁编辑器
      }
    }
  </script>
  <style src="@wangeditor/editor/dist/css/style.css"></style>
  <style lang="less">
     .home{
      width: 1260px;
      height: 1000px;
      margin: auto;
     }
     .ruledemo-Form{
      padding: 20px;
     }
  </style>
  